@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Regular"), url("../fonts/Roboto-Regular.ttf") format("ttf"); }

@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Medium"), url("../fonts/Roboto-Medium.ttf") format("ttf"); }

@font-face {
  font-weight: 700;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Bold"), url("../fonts/Roboto-Bold.ttf") format("ttf"); }

body {
  min-width: 414px;
  color: #e0e0e0;
  font-weight: 400;
  font-size: 18px;
  font-family: "Roboto", "Arial", sans-serif;
  line-height: 1.5em;
  background-color: #08020d; }

.img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.list-reset {
  margin: 0;
  padding: 0;
  line-height: 1.2em;
  list-style: none; }

.container {
  box-sizing: border-box;
  width: 414px;
  margin: 0 auto;
  padding: 0 20px; }

@media (min-width: 1240px) {
  .container {
    width: 1200px;
    padding: 0; } }

.relative {
  position: relative; }

.half-left {
  left: 50%; }

.translate-left-50 {
  transform: translateX(-50%); }

.my-0 {
  margin-top: 0;
  margin-bottom: 0; }

.mb-100 {
  margin-bottom: 100px; }

.mb-23 {
  margin-bottom: 23px; }

.mb-18 {
  margin-bottom: 18px; }

.mb-16 {
  margin-bottom: 16px; }

.ml-16 {
  margin-left: 16px; }

.pt-100 {
  padding-top: 100px; }

.full-width {
  grid-column: 1 / -1; }

.uppercase {
  text-transform: uppercase; }

.fs-16 {
  font-size: 16px; }

.fs-18 {
  font-size: 18px; }

.fs-30 {
  font-size: 30px; }

.lh-150 {
  line-height: 1.5em; }

.white {
  color: #fff; }

.text-center {
  text-align: center; }

.page-header {
  position: absolute;
  top: 25px;
  left: 50%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  transform: translateX(-50%); }

@media (min-width: 1240px) {
  .page-header {
    flex-direction: row;
    align-items: flex-start; } }

.logo {
  display: flex;
  align-items: center;
  margin-bottom: 17px;
  color: #fff;
  font-weight: 700;
  font-size: 11px;
  line-height: 1em;
  text-decoration: none; }

@media (min-width: 1240px) {
  .logo {
    margin-bottom: 0; } }

.main-nav-link {
  color: #e0e0e0;
  font-weight: 700;
  font-size: 11px;
  text-transform: uppercase;
  text-decoration: none; }

.main-nav-link-active {
  color: #fff; }

@media (min-width: 1240px) {
  .main-nav {
    max-width: 745px;
    margin-bottom: 0;
    margin-left: 88px; }
  .main-nav-list {
    display: flex;
    flex-wrap: wrap; }
  .main-nav-item {
    margin-right: 24px; } }

.search-field {
  box-sizing: border-box;
  width: 100%;
  padding: 5px 10px;
  border: none;
  border-radius: 50px;
  color: #fff;
  font-size: 13px;
  line-height: 1.2em;
  background-color: #3c3b3d; }

@media (min-width: 1240px) {
  .search-form {
    width: 248px;
    margin-left: auto; } }

.subscribe {
  padding: 248px 0 200px;
  color: #fff;
  background-image: url(../../images/banner.png);
  background-position: center;
  background-size: cover; }

.subscribe-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 570px;
  margin: 0 auto; }

.subscribe-field {
  box-sizing: border-box;
  width: 100%;
  min-width: 270px;
  min-height: 50px;
  margin-bottom: 1.5rem;
  padding: 0 20px;
  border: 2px solid #cacaca;
  border-radius: 3px;
  color: #fff;
  background-color: transparent;
  filter: drop-shadow 0 4px 4px rgba(0, 0, 0, 0.25); }

@media (min-width: 1240px) {
  .subscribe {
    padding-top: 200px; }
  .subscribe-title {
    font-size: 60px; }
  .subscribe-subtitle {
    margin-bottom: 32px;
    font-size: 38px; }
  .subscribe-field {
    width: 45%; } }

.similar-artists {
  display: grid;
  grid-row-gap: 25px;
  grid-column-gap: 50px;
  grid-template-columns: repeat(2, 1fr); }

@media (min-width: 1240px) {
  .similar-artists {
    grid-template-columns: repeat(4, 1fr); }
  .similar-artists-title {
    font-size: 38px; } }

.similar-item {
  width: 137px;
  text-align: center; }

.similar-img {
  margin-bottom: 18px; }

@media (min-width: 1240px) {
  .similar-item {
    width: 250px; }
  .similar-img {
    width: inherit;
    height: auto; } }

.btn {
  width: 100%;
  padding: 20px 30px;
  border: none;
  border-radius: 5px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  filter: drop-shadow 0 4px 4px rgba(0, 0, 0, 0.25); }

.btn-subscribe {
  color: #fff;
  font-size: 0.74em;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #34c176; }
